html {
    background-image: url('/img/background.svg');
}


// desktop 1920px
@media screen and(min-width: 1920px) {
    header {
        padding: 23px 0;
        border-bottom: 1px solid var(--secondary-light-gray);

        .wrapper {
            width: 100%;
            max-width: 1750px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin: 0 auto;
        }

        .location {
            display: flex;
            align-items: center;

            .location-img {
                padding: 0 20px 0 0;
            }

            .location-text {
                min-width: 145px;
                word-break: break-all;
            }
        }

        .links {
            display: flex;

            .link {
                text-decoration: none;
                padding: 26px 25px;
                transition: all 0.2s linear;

                &:hover {
                    transition: all 0.2s linear;
                    color: var(--primary-white);
                    background-color: var(--primary-black);
                }
            }
        }

        .phone {
            display: flex;
            align-items: center;

            .phone-img {
                padding: 0 17px 0 0;
            }

            .phone-number {
                min-width: 145px;
                word-break: break-all;
                text-decoration: none;
            }
        }
    }

    .top_menu {
        position: relative;
        padding: 30px 0;
        border-bottom: 1px solid var(--secondary-light-gray);

        .wrapper {
            max-width: 1750px;
            display: flex;
            justify-content: space-between;
            margin: 0 auto;
        }

        .top_menu-left {
            display: flex;

            .burger-menu {
                display: flex;
                align-items: center;
                margin: 0 60px 0 0;

                .burger-button {
                    display: flex;
                    align-items: center;
                    background-color: transparent;
                    border: none;
                    cursor: pointer;
                    transition: all 0.2s linear;
                    padding: 20px 20px 20px 0;

                    .burger {
                        margin: 0 18px 0 0;
                        display: inline-block;
                    }

                    .burger-text {
                        font-weight: 500;
                    }

                    &:hover {
                        transition: all 0.2s linear;
                        color: var(--secondary-light-gray);
                    }
                }

            }

            .links {
                display: flex;
                align-items: center;

                .link {
                    padding: 20px 25px;
                    text-decoration: none;
                    transition: all 0.2s linear;

                    &:hover {
                        transition: all 0.2s linear;
                        color: var(--secondary-light-gray);
                    }
                }
            }
        }

        .top_menu-logo {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        .top_menu-right {
            display: flex;
            align-items: center;

            .links {
                display: flex;
                align-items: center;

                .link {
                    background-color: transparent;
                    border: none;
                    cursor: pointer;
                    margin: 0 17.5px;
                    transition: all 0.2s linear;

                    &:last-child {
                        margin: 0 0 0 17.5px;
                    }
                }
            }
        }
    }

    footer {
        .wrapper {
            max-width: 1200px;
            margin: 0 auto;
            display: grid;
            grid-auto-columns: 1fr;
            grid-template-columns: 255px 232px 290px 245px 140px;
            gap: 0 0;

            .block_links {
                padding: 75px 20px;

                .title {
                    padding: 0 0 20px;
                    font-weight: 600;
                }

                .links {
                    display: flex;
                    flex-direction: column;

                    .link {
                        text-decoration: none;
                        margin: 0 0 10px 0;
                        transition: all 0.2s linear;
                        width: auto;
                        word-break: break-all;

                        &:hover {
                            transition: all 0.2s linear;
                            color: var(--secondary-light-gray);
                        }
                    }
                }
            }
        }

        .line {
            border-bottom: 1px solid var(--secondary-gray);
        }

        .wrapper-two {
            display: flex;
            max-width: 1760px;
            margin: 0 auto;
            justify-content: space-between;
            padding: 15px 57px;
        }
    }
}


