                    padding: 12px 17.5px;
                    text-decoration: none;
                    transition: all 0.2s linear;
                    font: var(--body-3);

                    &:hover {
                        transition: all 0.2s linear;
                        color: var(--secondary-light-gray);
                    }
                }
            }
        }

        .top_menu-logo {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            .button-logo {
                .logo {
                    width: 227px;
                }
            }
        }

        .top_menu-right {
            display: flex;
            align-items: center;

            .links {
                display: flex;
                align-items: center;

                .link {
                    background-color: transparent;
                    border: none;
                    cursor: pointer;
                    margin: 0 15px;
                    transition: all 0.2s linear;

                    &:last-child {
                        margin: 0 0 0 17.5px;
                    }
                }
            }
        }
    }

    footer {
        .wrapper {
            max-width: 866px;
            margin: 0 auto;
            display: grid;
            grid-auto-columns: 1fr;
            grid-template-columns: 184px 165px 213px 180px 180px;
            gap: 0 0;

            .block_links {
                padding: 75px 20px;

                .title {
                    padding: 0 0 20px;
                    font-weight: 600;
                    font: var(--sub-title-2);
                }

                .links {
                    display: flex;
                    flex-direction: column;

                    .link {
                        font: var(--body-4);
                        text-decoration: none;
                        margin: 0 0 10px;
                        transition: all 0.2s linear;
                        width: auto;
                        word-break: break-all;
                    }
                }
            }
        }

        .line {
            border-bottom: 1px solid var(--secondary-gray);
        }

        .wrapper-two {
            display: flex;
            max-width: 1306px;
            margin: 0 auto;
            justify-content: space-between;
            padding: 15px 57px;

            p {
                font: var(--body-4);
            }
        }
    }
}


// mobile 768px
@media screen and(min-width: 768px) and(max-width: 1366px) {
    header {
        display: none;
    }

    .top_menu {
        padding: 20px 0;
        border-bottom: 1px solid var(--secondary-light-gray);

        .wrapper {
            display: flex;
            justify-content: space-between;
            align-items: center;
            position: relative;
            max-width: 708px;
            margin: 0 auto;

            .top_menu-left {
                display: flex;

                .burger-button {
                    background-color: transparent;
                    border: none;

                    img {
                        width: 11.25px;
                    }
                }

                .burger-text {
                    display: none;
                }

                .links {
                    display: none;
                }
            }

            .top_menu-logo {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);

                .button-logo {
                    display: flex;
                    align-items: center;

                    .logo {
                        width: 127px;
                    }
                }

            }

            .top_menu-right {
                .links {
                    display: flex;
                    align-items: center;
                    .link {
                        height: 11.25px;
                        background-color: transparent;
                        border: none;
                        padding: 0;

                        img {
                            width: 11.25px;
                        }
                    }

                    .search {
                        margin: 0 10px 0 0;
                    }

                    .shopping {
                        margin: 0 0 0 10px;
                    }

                    .people,
                    .heart {
                        display: none;
                    }
                }
            }
        }
    }

    footer {
        .wrapper {
            max-width: 480px;
            margin: 0 auto;
            padding: 60px 0 40px;
            display: grid;
            grid-auto-columns: 1fr;
            grid-template-columns: 174px 154px 153px;
            gap: 0 0;

            .block_links {
                margin: 0 0 40px;

                .title {
                    font: var(--body-4);
                    margin: 0 0 12px 0;
                    font-weight: 600;
                }

                .links {
                    display: flex;
                    flex-direction: column;

                    .link {
                        font: var(--body-4);
                        text-decoration: none;
                        margin: 0 0 3px 0;

                        &:hover {
                            color: var(--secondary-light-gray);
                        }
                    }
                }
            }
        }

        .line {
            border-bottom: 1px solid var(--secondary-light-gray);
        }

        .wrapper-two {
            margin: 0 auto;
            max-width: 518px;
            padding: 15px 0;

            p {
                padding: 5px 0;
                font: var(--body-4);
            }
        }
    }
}


